/* basic-start */

html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: initial;
}

img {
  display: block;
  max-width: 100%;
  height: auto;
}

body {
  font-family: Arial, Helvetica, sans-serif;
  font-size: 16px;
  line-height: 26px;
  background: #fff;
}

.container {
  margin: 0 auto;
}

.list {
  padding: 0;
  margin: 0;
  list-style: none;
}

.title {
  padding: 0;
  margin: 0;
}

/* basic-end */

.section {
  background-image: url("./img/map3.png");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  height: 100vh;
}

.mdc-btn {
  min-width: 88px;
  height: 36px;
  padding: 0 16px;
  border: 0;
  outline: 0;
  border-radius: 2px;
  color: #fff;
  font-size: 14px;
  text-transform: uppercase;
  font-weight: 600;
  background-color: #00796b;
  cursor: pointer;
  box-shadow: 0 4px 4px 0 rgba(#414141, 0.3);
}
.mdc-btn:hover {
  background-color: #004c40;
}
.mdc-btn:active {
  box-shadow: 0 6px 4px 0 rgba(#414141, 0.3);
}

.search-form {
  display: flex;
  justify-content: center;
  padding: 20px;
  /* box-shadow: 0 0 2px 2px rgba(#414141, 0.2); */
  position: relative;
  z-index: 100;
  padding-top: 50px;
}

.search-form input {
  padding: 4px;
  outline: 0;
  border: 0;
  color: #263238;
  font-size: 18px;
  background-color: transparent;
  border-bottom: 2px solid #00796b;
}
.search-form input::placeholder {
  color: #616161;
}

.img {
  margin: 0 auto;
  width: 300px;
  height: 200px;
}

/* animation */
.cloud {
  width: 400px;
  height: 400px;
  background-image: url("./img/cloud.png");
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;

  animation: moveCloud 60s linear infinite;
}
.opacity {
  position: absolute;
  position: fixed;
  animation: test 60s linear infinite;
  z-index: 0;
}
.opacity:nth-child(1n) {
  top: -10%;
  left: 0%;
}
.opacity:nth-child(2n) {
  top: 30%;
  left: 30%;
}
.opacity:nth-child(3n) {
  top: -10%;
  left: 50%;
}

@keyframes moveCloud {
  0% {
    transform: translate(-400px, -100px);
  }
  99% {
    transform: translate(600px, 400px);
  }
  100% {
    transform: translate(-400px, -100px);
  }
}

@keyframes test {
  0% {
    opacity: 0;
  }
  5% {
    opacity: 0.6;
  }

  98% {
    opacity: 0;
  }
  100% {
    opacity: 0;
  }
}
.result {
  background-color: #00796b;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 30px;
  color: #ffffff;
  min-height: 60px;
  width: 500px;
  margin: 0 auto;
}
.result img {
  width: 40px;
  height: 40px;
  border: 2px solid #fff;
  margin-left: 5px;
  font-size: 18px;
}

@media (min-width: 320px) and (max-width: 767px) {
  .container {
    width: 320px;
  }
  .search-form {
    flex-direction: column;
  }
  .search-form input {
    margin-bottom: 17px;
  }
  .result {
    width: 280px;
  }
}

@media (min-width: 768px) {
  .container {
    width: 600px;
  }
  .search-form input {
    margin-right: 12px;
    margin-bottom: 10px;
  }
}

@media (min-width: 1170px) {
  .container {
    width: 1170px;
  }
}
